import React, { useState, useEffect } from 'react';
import { useLocation } from 'react-router-dom';
import { getMemberNo } from '../../../../api/farmApi';
import styled from 'styled-components';
import DiaryTitle from '../../../../components/diary/DiaryTitle';
import DiaryCalendar from '../../../../components/diary/DiaryCalendar';
import CalendarIcon from '../../../../components/diary/CalendarIcon';
const baseUrl = process.env.REACT_APP_BASE_URL;

const StyledContainer = styled.div`
    color: black;
    font-size: 0.8em;
    margin: auto 1.5rem auto;
    margin-top: 2rem;
`;

const DiaryCalendarPage = () => {
    const [memberData, setMemberData] = useState(null); // ÎÜçÎ∂ÄÏùò memberNo

    const location = useLocation(); // ÌòÑÏû¨ ÏúÑÏπò
    const { cropNo, cropNickname, buyDate } = location.state;

    console.log(cropNickname);
    console.log(buyDate);

    useEffect(() => {
        // ÏÑúÎ≤ÑÏóêÏÑú ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥ Í∞ÄÏ†∏Ïò§Í∏∞
        getMemberNo()
            .then((res) => {
                setMemberData(res.memberNo);
            })
            .catch((error) => {
                console.log('Îç∞Ïù¥ÌÑ∞ ÏïàÏò¥!!!!!!');
                console.error(error);
            });
    }, [memberData]);

    return (
        <>
            <StyledContainer>
                <DiaryTitle
                    memberNo={memberData}
                    cropNo={cropNo}
                    baseUrl={baseUrl}
                />
                <DiaryCalendar
                    memberNo={memberData}
                    cropNo={cropNo}
                    baseUrl={baseUrl}
                    cropNickname={cropNickname}
                    buyDate={buyDate}
                />
            </StyledContainer>
            <CalendarIcon />
        </>
    );
};
export default DiaryCalendarPage;
