import React from 'react';
import { Link } from 'react-router-dom';

export default function Button(props) {
    const buttonStyle = {
        fontSize: props.fontSize || '0.7rem', // ÏõêÌïòÎäî Í∏ÄÏî® ÌÅ¨Í∏∞Î°ú Ï°∞Ï†à
        backgroundColor: props.backgroundColor || '#90C8AC', // ÏõêÌïòÎäî Î∞∞Í≤ΩÏÉâÏúºÎ°ú Ï°∞Ï†à
        hoverBackgroundColor: props.hoverBackgroundColor || '#C4DFAA', // ÏõêÌïòÎäî Ìò∏Î≤Ñ Î∞∞Í≤ΩÏÉâÏúºÎ°ú Ï°∞Ï†à
    };

    const buttonClassName = `
    rounded-md
    px-2.5
    py-1.5
    text-sm
    font-semibold
    text-white
    shadow-sm
    ring-1
    ring-inset
    ring-gray-300
    hover:bg-${buttonStyle.hoverBackgroundColor}
  `;
    const handleClick = () => {
        // propsÎ°ú Ï†ÑÎã¨Î∞õÏùÄ onClick Ìï®Ïàò Ìò∏Ï∂ú
        if (props.onClick) {
            props.onClick();
        }
    };

    // ÎßåÏïΩÏóê to propÏù¥ Ï†ÑÎã¨ÎêòÎ©¥ Link Ïª¥Ìè¨ÎÑåÌä∏Î°ú Í∞êÏã∏Í≥†, ÏïÑÎãàÎ©¥ ÏùºÎ∞ò buttonÏùÑ Î†åÎçîÎßÅÌï©ÎãàÎã§.
    const ButtonElement = props.to ? (
        <Link
            to={props.to}
            style={{ ...buttonStyle }}
            className={buttonClassName}
        >
            {props.name}
        </Link>
    ) : (
        <button
            type="button"
            style={{ ...buttonStyle }}
            className={buttonClassName}
            onClick={handleClick}
        >
            {props.name}
        </button>
    );

    return <>{ButtonElement}</>;
}
